.description {
    padding: 40px 20px 20px;
    background-color: $grey;
    display: grid;
    
    & h2 {
        @include heading-2;
        @media (min-width: $desktop-width) {
            display: block;
            width: 100%;
        }
    }

    & p {
        @include plain-text;

        @media (min-width: $desktop-width) {
            display: block;
            width: 50%;
            margin-top: 0;
        }
    }

    & img {
        width: 100%;
        margin-bottom: 40px;
        @media (min-width: $desktop-width) {
            width: 40%;
        }
    }

    @media (min-width: $desktop-width) {
        grid-row: 2/3;
        grid-column: 1/3;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: flex-start;
        padding: 120px 40px 20px;
    }
}

.description img:nth-of-type(2) {
    grid-row: 5/6;
    margin-bottom: 0;
}